import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getters'
import mutations from './mutations'

Vue.use(Vuex)

const state = {
  address: [' '],
  carts: [{'CategoryId': '1', 'GoodsName': '', 'GoodsPrice': 9, 'GoodsNum': 1, 'GoodsImage': './static/img/github.png'}],
  comname: 'index',
  ordercur: 1,
  tabindex: 0
}

export default new Vuex.Store({
  state,
  getters,
  mutations
})



